import headers from "../../components/header_nav.vue"
export default{
	components:{
		headers
	},
	data(){
		return{
			Htitle: "京东商城",
			Hlicon: "../../static/icon/left_whitch.png",
			page:0,
			pages:1,
			loadshow:true,
			lists:[]
		}
	},
	methods:{
		 butBuy(e){
			uni.setStorageSync('webUrl', e);
			// setTimeout(() => {
				uni.navigateTo({
					url: "/pages/index/webview"
				});
				// window.location.href = dailyCheckinweb;
			// }, 1500)
		},
		Loadthepage() {
			var self = this;
			if(!self.loadshow){
				return
			}
			if (self.page <= self.pages) {
				var page = self.page + 1;
				self.$service.auth(self , self.$service.api.record.jdList, "GET" , {
					page: page,
					limit: 15
				}, function(self, res) {
					console.log(res , "res")
					self.pages = res.data.last_page;
					self.page = page;
					// self.activity = res.data.activity
					var list = res.data.data;
					list.forEach((v, i) => {
						self.lists.push(v);
					});
					// self.$forceUpdate()
					if (self.pages == self.page || self.lists.length == 0) {
						self.loadshow = false
						uni.showToast({
							title: '数据已加载完毕',
							icon: 'none'
						});
						
					} else {
						self.loadshow = true
						
					}
				});
			} else {
				self.loadshow = false
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
				
			}
		}
	},
	onReachBottom(){
		this.Loadthepage();
		console.log("onReachBottom");
	},
	onShow() {
		this.Loadthepage();
	}
	
}
